import Component from "vue-class-component";
import axios from "axios";
import * as indexApi from "../../api/indexApi";
import moment from "moment";

import corpperlabel from '../transmit/corpperlabel'

@Component({
  props: {},
  components: {
    corpperlabel
  }
})
export default class AddGoods extends Vue {

    viewBigIcon(qrcode) {
    this.bigIcon = qrcode;
    this.viewBig = true;
  }
  bigIcon = "";
  viewBig = false;
handledataType1(dataType){
  switch(dataType){
    case 'TRUTH':
    return 'success';
   case 'FICTITIOUS':
    return 'warning';  
    case 'NONENTITY':
    return 'danger';
    default: return ''
  }
}
handledataType(dataType){
  switch(dataType){
    case 'TRUTH':
    return '药品库';
   case 'FICTITIOUS':
    return '虚拟库';  
    case 'NONENTITY':
    return '不在目录';
    default: return ''
  }
}
  page = 0;
  loading = false;
  total = 0;
  drugList = [];
  pageSize = 10;
  keyword = "";
  onPageChange(page) {
    this.page = page - 1;
    this.requireDrugList();
  }
  date = []
  dataType=""
  requireDrugList(filter = null) {
    if (filter) {
      this.page = 0;
    }
    this.loading = true;
    indexApi
      .requireDrugList({
        keyword: this.keyword,
        startqueryTime:this.date && this.date.length>0? moment(this.date[0]).format("YYYY-MM-DD") + " 00:00:00":"",
        endqueryTime:  this.date && this.date.length>0? moment(this.date[1]).format("YYYY-MM-DD") + " 23:59:59":"",
        dataType:this.dataType,
        page: this.page,
        pageSize: this.pageSize
      })
      .then(res => {
        
        if (res["retCode"]) {
          this.drugList = res.data.InquireDrugsRecords;
          this.total = res.data.page.total;
        } else {
          if (!res["islogin"]) {
            this.$alert(res["message"]);
          }
        }
        this.loading = false;
      });
  }
  handledrugFlag(falg) {
    if (falg === "1") {
      return "医生";
    } else {
      return "患者";
    }
  }

  /**
   *
   * 详情
   */
  detailModel = false;
detail={
  row:{},
  remark:""
}
  selectDrug(index, row) {
    this.detail.row = row

    this.detail.row['picturesUrlList1'] = this.detail.row['picturesUrlList'].map(item=>{
      return {
        presImageUrl:item
      }
    })
    
  if(  this.detail.row['picturesUrlList'].length>0){
    (<any>this.$refs.cropper).changePreImageUrl(0)
  }

    this.detail.remark = row.remark;
    this.detailModel = !this.detailModel
  }

  doRemark(){
    this.loading = true
  indexApi
      .updateInquireDrugsRecords({
        allDrugId:this.detail.row['allDrugId'],
        remark: this.detail.remark
      })
      .then(res => {
    this.loading = false
        if (res["retCode"]) {
          this.$message('保存成功')
              this.detailModel = !this.detailModel
              this.requireDrugList()
        } else {
          if (!res["islogin"]) {
            this.$alert(res["message"]);
          }
        }
        this.loading = false;
      });
    

  }

  mounted() {
    this.requireDrugList();
  }
}



